<template>
  <div class="content-body">
    <div class="container">
      <div class="row">
        <div class="col-xxl-12">
          <h2>NFT Minting</h2>
          <p>
            Mint some World of Masks NFTs on the testnet for free!
          </p>
          <button @click="mintWomNft(49)" class="btn btn-primary">Mint 50</button>
          &nbsp;
          <button @click="mintWomNft(24)" class="btn btn-primary">Mint 25</button>
          &nbsp;
          <button @click="mintWomNft(5)" class="btn btn-primary">Mint 5</button>
          <br />
          <br />
          <br />
          {{ feedbackMessage }}
          <br />
          <br />
          <br />

          <h2>Free Testnet BCH</h2>
          <p>Your current BCH balance:
            {{ Math.round(stakersBchBalance / 10**15) / 10**3 }} BCH
          </p>
          <br />
          <p>
            If you need testnet BCH, please claim it for free at:
            <a href="http://54.169.31.93:8080/faucet" target="_blank">http://54.169.31.93:8080/faucet</a>
          </p>
          <br />

          <div class="col-xxl-12">
            <h2>For test purposes, you can burn WoM and Pow tokens here:</h2>
            <p>Your WoM token balance: {{ Math.round(stakersWomBalance/10**15) / 10**3 }} WoM</p>
            <button @click="burnWomToken(0)" class="btn btn-primary">Burn My WoM</button>
          <br />
          <br />

            <p>Your Pow token balance: {{ stakersPowBalance }} Pow</p>
            <button @click="burnPowToken(0)" class="btn btn-primary">Burn My Pow</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>

import { ethers } from "ethers";
import WorldOfMasksNFT from "../../artifacts/contracts/WorldOfMasksNFT.sol/WorldOfMasksNFT.json";
import PowToken from "../../artifacts/contracts/PowToken.sol/PowToken.json";
import RewardToken from "../../artifacts/contracts/RewardToken.sol/RewardToken.json";


export default {
  name: 'App',
  components: {},
  data() {
    return {
      provider: null,
      signer: null,

      stakersAddress: "",
      feedbackMessage: "",

      powTokenContract: null,
      rewardTokenContract: null,

      stakersBchBalance: 0,
      stakersWomBalance: 0,
      stakersPowBalance: 0,
    }
  },
  methods: {
    burnWomToken: async function (amount) {
      this.feedbackMessage = "";
      if (amount == 0) {
        amount = this.stakersWomBalance;
      }
      if (amount == 0) {
        this.feedbackMessage = "Nothing to burn!";
        return false;
      }
      let tx = await this.rewardTokenContract.burn(amount);
      await tx.wait();
      this.load();
    },

    burnPowToken: async function (amount) {
      this.feedbackMessage = "";
      if (amount == 0) {
        amount = this.stakersPowBalance;
      }
      if (amount == 0) {
        this.feedbackMessage = "Nothing to burn!";
        return false;
      }
      let tx = await this.powTokenContract.burn(amount);
      await tx.wait();
      this.load();
    },

    getPowTokenBalance: function () {
      this.powTokenContract.balanceOf(this.stakersAddress).then((balance) => {
        this.stakersPowBalance = balance;
      });
    },

    getRewardTokenBalance: function () {
      this.rewardTokenContract.balanceOf(this.stakersAddress).then((balance) => {
        this.stakersWomBalance = balance;
      });
    },

    getBchBalance: function () {
      this.provider.getBalance(this.stakersAddress).then((balance) => {
        this.stakersBchBalance = balance;
      });
    },

    mintWomNft: async function (amount) {
      this.feedbackMessage = "Minting " + amount + " NFTs. Please wait!";
      let c = new ethers.Contract(window.WomNftAddress, WorldOfMasksNFT.abi, this.provider.getSigner());
      let tx = await c.mint(amount, {
        gasLimit: 9999999,
        gasPrice: 1046740000,
      });
      await tx.wait();
      window.location.reload();
    },

    load: async function () {
    this.signer.getAddress().then((myAddress) => {
      this.stakersAddress = myAddress;
      this.getPowTokenBalance();
      this.getBchBalance();
      this.getRewardTokenBalance();
    });
  },
  },
  mounted: function () {
    this.provider = new ethers.providers.Web3Provider(window.ethereum, 'any');
    this.provider.send("eth_requestAccounts", []).then(() => {
      this.signer = this.provider.getSigner();

      let _powTokenContract = new ethers.Contract(
        window.PowTokenAddress,
        PowToken.abi,
        this.provider.getSigner()
      );
      let _womContract = new ethers.Contract(window.RewardTokenAddress, RewardToken.abi, this.provider.getSigner());

      this.powTokenContract = _powTokenContract;
      this.rewardTokenContract = _womContract;
    }).then(() => {
      this.load();
    })
  }
}
</script>